    justify-content: flex-start;
    align-items: center; }
    @media screen and (min-width: 960px) {
      .compliments_container {
        flex-direction: row;
        justify-content: space-around; } }
  .compliments .compliment {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center; }
    .compliments .compliment_media-logo {
      width: 180px;
      margin-bottom: 40px; }
    @media screen and (min-width: 960px) {
      .compliments .compliment {
        width: 30%; } }
  .compliments .other-medias_logo {
    padding-top: 50px;
    width: 100%; }

